@import url('https://fonts.googleapis.com/css2?family=Alata&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&display=swap');

.header{
    display:flex;
    align-items:center;
    color:white;
    background-color: black;
    top:0px;
    position:sticky;
    z-index: 2000;
    
}

.header_left{
    flex:0.4;
}

.header_left >h1{
    text-align:center;
    font-family: "Alata",sans-serif;
    letter-spacing: 1px;
    padding:20px 0px;
}

.header_left>h1>span{
    color:#f64c08;
}

.header_right{
    flex:0.6;
    display:flex;
    justify-content: space-evenly;
    align-items: center;
}

.header_right> a> h4{
    font-size: 16px;
    cursor:pointer;
    transition: transform 200ms ease-in-out ;
    font-family: "Crimson Text",serif;
    padding:20px 0px;
    letter-spacing: 1px;
}


.header_right> a> h4:hover{
   transform:scale(1.09);
   font-family: "Crimson Text",serif;
   color:white;
   background-color: #f64c08;
   padding:5px 20px;
   letter-spacing: 1px;
   border-radius: 2px;
   font-size: 16px;
   cursor:pointer;
}

.header_right_button{
    background-color: #f64c08;
    color:white;
    padding: 5px 20px;
    border-radius: 2px;
    cursor: pointer;
}