
.contact{
    color:white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 200px 0 100px;
}
.contactContainer{
    border:1px solid white;
    width:600px;
    height:200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:30px;
    margin-top: 20px;

}
.contactContainer > p{
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;
}

.contactContainer > p > span{
    color:#f64c08;
}
.contactIcons > a > button {
    color:white !important;  
}
.contactIcons > a > button > span >.MuiSvgIcon-root{
    color:white !important;
    font-size: 20px;
}