@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300;600&display=swap');

.topContent{
    color:white;
    position: relative;
}

.topContent_Container{
    position:absolute;
    top:300px;
    right:150px;
}
.topContent_Container > p{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 3px;
    padding-top: 20px;
}

.topContent_Container > a > button {
    margin-top: 30px;
    margin-left: 5px;
    text-align: left;
    border:none;
    border-radius: 2px;
    outline: none;
}

.topContent_Container > h1 {
    font-size: 50px;
    font-family: "Montserrat",sans-serif;

}

.topContent_downloadBtn{
  color:white;
  background-color: #f64c08;
  font-size: 14px;
  padding:10px 30px;
  font-family: "Montserrat",sans-serif;
  font-weight: 300;
  cursor: pointer;
  margin-right: 20px;

}

.topContent_projectBtn{
    color:white;
    background-color: transparent;
    border:1px solid white !important;
    padding:9px 45px;
    font-size: 14px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;

}

.topContent_downloadBtn:hover{
    color:#f64c08;
    background-color:white;
    font-weight: 600;
     
  }

  .topContent_projectBtn:hover{
    color:black;
    background-color:white;
    font-weight: 600;
     
  }