@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata&family=Josefin+Sans&family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alata&family=Josefin+Sans&family=Montserrat:wght@300;600&display=swap);
*{
  margin:0px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    display:flex;
    align-items:center;
    color:white;
    background-color: black;
    top:0px;
    position:-webkit-sticky;
    position:sticky;
    z-index: 2000;
    
}

.header_left{
    flex:0.4 1;
}

.header_left >h1{
    text-align:center;
    font-family: "Alata",sans-serif;
    letter-spacing: 1px;
    padding:20px 0px;
}

.header_left>h1>span{
    color:#f64c08;
}

.header_right{
    flex:0.6 1;
    display:flex;
    justify-content: space-evenly;
    align-items: center;
}

.header_right> a> h4{
    font-size: 16px;
    cursor:pointer;
    transition: transform 200ms ease-in-out ;
    font-family: "Crimson Text",serif;
    padding:20px 0px;
    letter-spacing: 1px;
}


.header_right> a> h4:hover{
   transform:scale(1.09);
   font-family: "Crimson Text",serif;
   color:white;
   background-color: #f64c08;
   padding:5px 20px;
   letter-spacing: 1px;
   border-radius: 2px;
   font-size: 16px;
   cursor:pointer;
}

.header_right_button{
    background-color: #f64c08;
    color:white;
    padding: 5px 20px;
    border-radius: 2px;
    cursor: pointer;
}
.topContent{
    color:white;
    position: relative;
}

.topContent_Container{
    position:absolute;
    top:300px;
    right:150px;
}
.topContent_Container > p{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 3px;
    padding-top: 20px;
}

.topContent_Container > a > button {
    margin-top: 30px;
    margin-left: 5px;
    text-align: left;
    border:none;
    border-radius: 2px;
    outline: none;
}

.topContent_Container > h1 {
    font-size: 50px;
    font-family: "Montserrat",sans-serif;

}

.topContent_downloadBtn{
  color:white;
  background-color: #f64c08;
  font-size: 14px;
  padding:10px 30px;
  font-family: "Montserrat",sans-serif;
  font-weight: 300;
  cursor: pointer;
  margin-right: 20px;

}

.topContent_projectBtn{
    color:white;
    background-color: transparent;
    border:1px solid white !important;
    padding:9px 45px;
    font-size: 14px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;

}

.topContent_downloadBtn:hover{
    color:#f64c08;
    background-color:white;
    font-weight: 600;
     
  }

  .topContent_projectBtn:hover{
    color:black;
    background-color:white;
    font-weight: 600;
     
  }
.topContainer{
   background:url(/portfolio/static/media/PmdesY.1751b1a1.png);
   height:100vh;
   background-position: center;
   background-size: cover;
   background-repeat: no-repeat;
   box-shadow: inset 0 0 0 2000px rgba(0,0,0,0.5);
}
.skillContainer{
    display:flex;
    align-items: center;
    justify-content: center;
    margin: 200px 0px;
}

.skillContainer_image{
    flex:0.4 1;
    text-align: center;
    margin-right: 100px;
}

.skillContainer_image > img{
    width:600px;
    object-fit: contain;
    border-radius: 3px;
}

.skillContainer_text{
    flex:0.6 1;
    color:white;
    max-width: 500px;
    text-align:left;
    margin-bottom: 20px;
}

.skillContainer_text > h2{
  font-size: 3opx;
}

.skillContainer_skillset > h5{
    font-family:"Montserrat",sans-serif ;
    font-weight: 300;
    padding-bottom: 30px;
    padding-top: 10px;
  
  }

  .skillContainer_slider > .MuiLinearProgress-colorPrimary{
      background-color: rgb(88,88,88);

  }

  .skillContainer_slider1 > span >.MuiLinearProgress-barColorPrimary{
    background-color:tomato;
}

.skillContainer_slider2 > span >.MuiLinearProgress-barColorPrimary{
    background-color:red;
}

.skillContainer_slider3 > span >.MuiLinearProgress-barColorPrimary{
    background-color:yellow;
}

.skillContainer_slider4 > span >.MuiLinearProgress-barColorPrimary{
    background-color:green;
}

.skillContainer_slider5 > span >.MuiLinearProgress-barColorPrimary{
    background-color:orange;
}

 

  
.Projects > img{
    width:350px;
    height:179px;
}

.Projects {
    margin:15px;
    width:350px;
    height:179px;
    transition:transform 100ms ease-in;

}

.Projects:hover{
    color:white;
    background-color: #f64c08;
    transform:scale(1.05);
    display:flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.Project_Content{
    padding: 10px;
}

.Project_Content > h4 {
    padding-bottom: 5px;

}

.Project_Content > p {
    font-size: 15px;
}
.projectContainer{
    color:white;
    display:flex;
    flex-direction: column;
    justify-content: center;
    margin:200px 0px;
    align-items: center;

}

.projectContainer > h1{
    padding-bottom: 15px;
}

.projectContainer > p{
    padding-bottom: 20px;
}

.projectContainer_projects{
    display:flex;
    width:60vw;
    margin-top: 50px;
    flex-wrap: wrap;
}
.experianceBox{
    
    display:flex;
    flex-direction: column;
    border:2px dotted white;
    border-radius: 10px;
    width:200px;
    height:150px;
    justify-content: center;
    align-items: center;
    background-color: black;
    overflow: hidden;
}

.experianceBox{
    font-family: "Josefin Sans",sans-serif;
    font-weight: 16px;
    letter-spacing: 1px;
}
.experianceContainer{
    color:white;
    display:flex;
    flex-direction: column;
    justify-content: center;
    margin:200px 0px;
    align-items: center;
    
}
.experianceContainer > h1{
    padding-bottom: 20px;
}

.experianceContainer_info{
    background: url(/portfolio/static/media/PmdesY.1751b1a1.png);
    background-position: center;
    background-size: cover;
    height:300px;
    z-index: 1000px;
    width:80vw;
    box-shadow: inset 0 0 0 2000px rgba(0,0,0,0.9);
    display: flex;
    justify-content:space-evenly;
    align-items: center;

}

.contact{
    color:white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 200px 0 100px;
}
.contactContainer{
    border:1px solid white;
    width:600px;
    height:200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:30px;
    margin-top: 20px;

}
.contactContainer > p{
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;
}

.contactContainer > p > span{
    color:#f64c08;
}
.contactIcons > a > button {
    color:white !important;  
}
.contactIcons > a > button > span >.MuiSvgIcon-root{
    color:white !important;
    font-size: 20px;
}
a {
  text-decoration: none;
  color:white !important;
}
