@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:wght@300;600&display=swap');

.skillContainer{
    display:flex;
    align-items: center;
    justify-content: center;
    margin: 200px 0px;
}

.skillContainer_image{
    flex:0.4;
    text-align: center;
    margin-right: 100px;
}

.skillContainer_image > img{
    width:600px;
    object-fit: contain;
    border-radius: 3px;
}

.skillContainer_text{
    flex:0.6;
    color:white;
    max-width: 500px;
    text-align:left;
    margin-bottom: 20px;
}

.skillContainer_text > h2{
  font-size: 3opx;
}

.skillContainer_skillset > h5{
    font-family:"Montserrat",sans-serif ;
    font-weight: 300;
    padding-bottom: 30px;
    padding-top: 10px;
  
  }

  .skillContainer_slider > .MuiLinearProgress-colorPrimary{
      background-color: rgb(88,88,88);

  }

  .skillContainer_slider1 > span >.MuiLinearProgress-barColorPrimary{
    background-color:tomato;
}

.skillContainer_slider2 > span >.MuiLinearProgress-barColorPrimary{
    background-color:red;
}

.skillContainer_slider3 > span >.MuiLinearProgress-barColorPrimary{
    background-color:yellow;
}

.skillContainer_slider4 > span >.MuiLinearProgress-barColorPrimary{
    background-color:green;
}

.skillContainer_slider5 > span >.MuiLinearProgress-barColorPrimary{
    background-color:orange;
}

 

  