.Projects > img{
    width:350px;
    height:179px;
}

.Projects {
    margin:15px;
    width:350px;
    height:179px;
    transition:transform 100ms ease-in;

}

.Projects:hover{
    color:white;
    background-color: #f64c08;
    transform:scale(1.05);
    display:flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.Project_Content{
    padding: 10px;
}

.Project_Content > h4 {
    padding-bottom: 5px;

}

.Project_Content > p {
    font-size: 15px;
}